html {
  background: rgb(80, 173, 234);
}

.section-1 {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 5vh auto;
}

.section-1 div {
  background: black;
  height: 100px;
  width: 100px;
  border-radius: 0 30px 0 30px;
}

.section-1 div:hover {
  scale: 1.03;
  opacity: 0.8;
}

.section-2 {
  display: flex;
  justify-content: space-around;
}

.section-2 div {
  background: white;
  height: 50px;
  width: 50px;
  border-radius: 50%;
}

.section-2 div:hover {
  scale: 0.95;
  opacity: 0.8;
}